@import "styles/globalColors.scss";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 4px;
  text-decoration: none;
}
html {
  background-color: $bg;
  max-width: 1920px;
  max-height: 1080px;
  width: 100%;
  height: 100%;
  position: relative;
  #root {
    width: 100%;
    height: 100%;
  }
}
